import { h } from 'preact'
import User from './User.jsx'
import LinkedIn from '../social/LinkedIn/index.jsx'
import GitHub from '../social/GitHub/index.jsx'
import Ellipsis from '../ellipsis.jsx'

/**
 * The buttons to authenticate user.
 * @param {Object} props
 * @param {Auth} props.auth
 */
const AppUser = ({
  linkedin = true, privacy, error, loading, auth, host, signOut,
}) => {
  if (error)
    return (h('div',{},`Error: `,error))
  if (loading)
    return (h('div',{},`Loading`,h(Ellipsis)))

  const loggedIn = auth.linkedin_user || auth.github_user
  return (h('div',{},
    !loggedIn && h('span',{'style':"display:block"},`To display the profile image and validate your GitHub profile, sign in. No advanced permissions are required other than default ones (no email). Your public LinkedIn ID remains unknown. `,h('a',{'href':privacy},`Privacy Policy`)),

    h(User,{auth:auth,signOut:signOut}),

    linkedin && !auth.linkedin_user && h(LinkedIn,{host:host}),
    linkedin && !auth.linkedin_user && ' ',
    !auth.github_user && h(GitHub,{host:host}),
  ))
}

export default AppUser