import { h } from 'preact'
import Form, {
  FormGroup, Input, TextArea, SubmitButton, SubmitForm,
} from '@depack/form'
import { getUserData } from '../Auth/lib'
import Subscriptions from './Subscriptions.jsx'

export default class CommentForm extends SubmitForm {
  constructor() {
    super()
    this.fetchOptions = { credentials: 'include' }
    this.state.country_code = undefined
  }
  async componentWillMount() {
    try {
      const {
        'country_code': country_code,
      } = await this.context.fetch('https://freegeoip.app/json/')
      if (country_code) {
        this.setState({ country_code })
      }
    } catch (err) {
      // ok
    }
  }
  /**
   * @param {!Object} [props]
   * @param {Auth} [props.auth]
   */
  render({ onChange, auth, scope, ...props
  }) {
    const { host, signedIn, setReply,
      replyTo: { id: replyTo, name: replyToName } = {} } = this.context
    const { formLoading, error, success } = this.state

    const { picture, name } = getUserData(auth)

    return (h(Form,{...props, onSubmit:this.submit.bind(this), onChange:values => {
      this.reset()
      if(onChange) onChange(values)
    }},
      picture && h(Input,{value:picture,type:"hidden",name:"photo"}),
      h(Input,{value:auth.csrf,type:"hidden",name:"csrf"}),
      h(FormGroup,{'form-row':true,'col-2':true,label:"Name*",help:"This will appear on the website"},
        h(Input,{disabled:!signedIn,value:name,'col-10':true,name:"name"}),
      ),
      h(FormGroup,{help:auth.github_user ? 'GitHub username, sign out to remove' : 'Please sign in with GitHub','form-row':true,'col-2':true,label:"GitHub"},
        h(Input,{value:auth.github_user ? auth.github_user.html_url : null,'col-5':true,name:"github",disabled:true}),
        auth.github_user && h('div',{'className':"form-check form-check-inline col-4"},
          h('input',{'type':"checkbox",'className':"form-check-input",'id':"defaultCheck2",'name':"hide-github"}),
          h('label',{'className':"form-check-label",'htmlFor':"defaultCheck2"},
            `Hide from public`
          ),
        ),
      ),
      h(FormGroup,{'form-row':true,'col-2':true,label:"Comment*",help:"Please enter your opinion"},
        h('div',{'className':"col-10"},
          h(TextArea,{disabled:!signedIn,'data-id':"comment-text-area",required:true,name:"comment"},
            replyToName && `${replyToName}, `,
            `I think you're right/wrong because...`
          ),
          replyTo && h('input',{'value':replyTo,'type':"hidden",'name':"reply-to"}),
        ),
        replyTo && h('div',{'className':"col-10"},
          `You're replying to `,h('a',{'onClick':(ev) => {
            ev.preventDefault()
            const el = document.querySelector(`[data-comment="${replyTo}"]`)
            el.scrollIntoView({ behavior: 'smooth' })
            return false
          },'href':"#"},
            replyToName,`.`
          ),` `,h('a',{'onClick':(ev) => {
            ev.preventDefault()
            setReply(null)
            return false
          },'href':"#"},
            `Clear response`
          ),
        ),
      ),
      h(Subscriptions,{disabled:!signedIn,host:host,scope:scope}),
      h(SubmitButton,{disabled:!signedIn,loading:formLoading,type:"warning",confirmText:"Submit Data"}),
      error && `Error: ${error}`,
      success && `Comment has been submitted!`,
    ))
  }
}

