import { h } from 'preact'
import { Component } from 'preact'
import './style.css'
import Ellipsis from '../ellipsis.jsx'

export default class List extends Component {
  constructor() {
    super()
    this.state = {
      /** @type {!Array<WebsiteComment>} */
      comments: [],
      page: 0,
      csrf: null,
    }
  }
  componentDidMount() {
    this.fetch()
  }
  /**
   * @param {string} [id]
   * @param {string} [lastCommentId]
   */
  async fetch(id, lastCommentId) {
    this.setState({ loading: true })
    const { responseTo } = this.props
    const query = {
      id,
      'reply-to': responseTo,
      'last-comment-id': lastCommentId,
    }
    try {
      const {
        'comments': comments,
        csrf,
      } = await this.context.fetch('comments', {
        credentials: 'include',
        query,
      })
      const c = lastCommentId ? [...this.state.comments, ...comments] :
        [...comments, ...this.state.comments]
      this.setState({ comments: c, csrf })
    } catch(err) {
      this.setState({ error: err.message })
    } finally {
      this.setState({ loading: false })
    }
  }
  render({ totalItems = Infinity }) {
    const { error, loading, comments, csrf } = this.state
    if (error)
      return (h('div',{},`Error loading list: `,error))

    return (h('div',{'className':"CommentsList"},
      comments.map((comment) => {
        return (h(Item,{list:this,key:comment._id,comment:comment,csrf:csrf,onRemove:(id) => {
          this.setState({
            comments: comments.filter(({ _id }) => {
              return _id != id
            }),
          })
        }}))
      }),
      loading && h('div',{},`Loading list`,h(Ellipsis)),
      !loading && !comments.length && h('div',{},`No comments yet.`),

      (!loading && !!comments.length && totalItems > comments.length) && h('a',{'onClick':(ev) => {
        ev.preventDefault()
        this.fetch(undefined, comments[comments.length - 1]._id)
        return false
      },'href':"#"},`Load more`),
    ))
  }
}

const Login = ({ github_user }) => {
  if (!github_user) return null
  return (h('span',{},` (`
    ,h('a',{'href':github_user.html_url},github_user.login),`)`
  ))
}

class Item extends Component {
  constructor() {
    super()
    this.list = null
  }
  /**
   * @param {!Object} [opts]
   * @param {!WebsiteComment} [opts.comment]
   */
  render({ comment: {
    _id, country, isAuthor, name, photo, comment, date, github_user,
    replies }, onRemove, csrf }) {
    const { signedIn, setReply } = this.context
    const { expandResponses } = this.state
    return (h('div',{'className':"comment"},
      h('strong',{},name || 'Anonymous'),h(Login,{github_user:github_user}),
      country ? ` from ${country}`: '',
      ' ',`on `,h('em',{},new Date(date).toLocaleString()),` `,isAuthor && h('a',{'onClick':async (e) => {
        e.preventDefault()
        const c = confirm('Are you sure you want to delete comment?')
        if (!c) return false
        try {
          await this.context.fetch(`remove-comment/${_id}`, {
            credentials: 'include',
            query: {
              'csrf': csrf,
            },
          })
          onRemove(_id)
        } catch(err) {
          alert(err.message)
        }
        return false
      },'className':"badge badge-danger",'href':"#"},
        `Remove`
      ),` `,h('a',{'style':signedIn ? undefined : 'color:grey' ,'className':"badge badge-primary",'href':"#",'onClick':(ev) => {
        ev.preventDefault()
        if (!signedIn) alert('Please sign in to reply')
        else setReply({ id: _id, name, expandResponses: (id) => {
          if (this.list) this.list.fetch(id)
          else this.setState({ expandResponses: true })
        } })
        return false
      }},`Reply`),
      h('div',{'data-comment':_id,'className':"LCommentBlock"},
        photo && h('div',{},
          h('img',{'src':photo}),
        ),
        h('div',{},
          comment,
          !expandResponses && h(ReplyBtn,{onClick:(ev) => {
            ev.preventDefault()
            this.setState({ expandResponses: true })
            return false
          },replies:replies}),
        ),
      ),
      expandResponses && h('div',{'data-comment':_id,'className':"LCommentBlock"},
        h('div',{'style':"width:50px;"}),
        h('div',{},
          h(List,{ref:(list) => {
            this.list = list
          },responseTo:_id,totalItems:replies}),
        ),
      ),
    ))
  }
}

const ReplyBtn = ({ replies, onClick }) => {
  if (!replies) return null
  return (h('div',{},
    h('a',{'onClick':onClick,'href':"#"},`Load `,replies,` repl`,replies > 1 ? 'ies' : 'y'),
  ))
}

/**
 * @suppress {nonStandardJsDocs}
 * @typedef {import('../../').WebsiteComment} WebsiteComment
 */